import React from "react";
import { useState } from "react";
import UserInfo from "../Profile/UserInfo";
import FriendlistBody from "./FriendlistBody";
import Form from "./FriendlistForm";
import Button from "../Generic-Components/Button";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import "./Friendlist.css";

function Friendlist({ name }) {
	//this is mock data for testing purposes
	const friends = [
		{
			nickname: "Bill",
			location: "London",
			rank: "1",
			uid: "aaasdbaeaaaaa",
		},
		{
			nickname: "Charlie",
			location: "London",
			rank: "2",
			uid: "adbadfnbwsd",
		},
		{
			nickname: "Percy",
			location: "London",
			rank: "3",
			uid: "ssdfsedbgwewf",
		},
		{
			nickname: "Fred",
			location: "London",
			rank: "4",
			uid: "sdfvsbrgvsdf",
		},
		{
			nickname: "George",
			location: "London",
			rank: "5",
			uid: "dfbofnboier",
		},
		{
			nickname: "Ron",
			location: "London",
			rank: "6",
			uid: "sdkmvoasdfirnbsd",
		},
		{
			nickname: "Ginny",
			location: "London",
			rank: "7",
			uid: "slkdfjsiobrsdf",
		},
	];

	//This creates an entry for each friend in the list
	var counter = 1;
	const listItems = friends.map((friend) => (
		<Link to={`/friend/${friend.uid}`}>
			<FriendEntry
				uid={friend.uid}
				name={friend.nickname}
				location={friend.location}
				rank={friend.rank}
				key={counter++}
			/>
		</Link>
	));

	//This toggles the button function for adding a friend
	const [showAddFriend, setShowAddFriend] = useState(false);

	return (
		<Router>
			<Switch>
				<Route
					path="/friend"
					exact={true}
					component={() => {
						return (
							<div>
								<UserInfo name={name} />
								<div className="body">
									<h2>Friend List</h2>
									<br />
									{showAddFriend ? <Form /> : ""}
									<Button
										text={
											showAddFriend
												? "Submit"
												: "Add Friend"
										}
										color="White"
										onClick={toggleAddFriend}
									/>
									<br></br>
									{listItems}
								</div>
							</div>
						);
					}}
				></Route>

				<Route
					path="/friend/:friendId"
					exact={true}
					component={hello}
				/>
			</Switch>
		</Router>
	);

	//this flips the state of the add friend function to show either add or submit
	function toggleAddFriend() {
		setShowAddFriend(!showAddFriend);
		console.log(showAddFriend);
	}
	function hello({ match }) {
		const target = friends.find((f) => f.uid === match.params.friendId);

		return (
			<div>
				<Link to="/friend">
					<Button text="back" color="white"></Button>
				</Link>
				<UserInfo
					name={target.nickname}
					uid={target.uid}
					rank={target.rank}
				/>
				<FriendlistBody />
			</div>
		);
	}
}
const divStyle = {
	color: "white",
};

//This is a component that is only used on this page to draw friend lists.
function FriendEntry({ uid, name, location, rank, counter }) {
	return (
		<div className="friend-stub" key={counter++}>
			<span className="stub-name" key={counter + "#1"}>
				{name}
			</span>

			<span className="stub-id" key={counter + "#7"}>
				{uid}
			</span>
			<span className="stub-location" key={counter + "#3"}>
				{location}
			</span>
			<span className="stub-rank" key={counter + "#5"}>
				Rank: <span key={counter + "#6"}>{rank}</span>
			</span>
			<hr key={counter} />
		</div>
	);
}

export default Friendlist;
