import React, { useState } from "react";
import { Button } from "react-bootstrap";

const ChoosePlay = () => {
  const [whichClick, setClick] = useState("");

  return renderPlay(whichClick);

  function renderPlay(whichClick) {
    switch (whichClick) {
      case "play":
        return (
          <p>Play</p>
        )
      case "create":
        return (
          <p>Create</p>
        )

      case "tutorial":
        return (
          <p>Tutorial</p>
        )

      default:
        return (
          <div id="choose">
            <Button variant="primary" size="lg" id="playgame" onClick={() => setClick("play")}block>
              Play Game
            </Button>
            <br />
            <Button variant="primary" size="lg" id="creategame" onClick={() => setClick("create")} block>
              Create Game
            </Button>
            <br />
            <Button variant="primary" size="lg" id="creategame" onClick={() => setClick("tutorial")} block>
              Tutorial
            </Button>
          </div>
        );
    }
  }
};

export default ChoosePlay;
