import React, { useContext, useState, useEffect } from "react";
import axios from "axios";

const BackendContext = React.createContext();

const api = axios.create({
  baseURL: "http://localhost:8080",
});

export function useServer() {
  return useContext(BackendContext);
}

export function BackProvider({ children }) {
  function createDBUser(email) {
    axios
      .post("http://localhost:8080/create-user", { data: email })
      .then((res) => {
        console.log(res);
      });
  }

  function updateDBUser(email, nickname, country) {
    axios
      .patch("http://localhost:8000/update-user", {
        email: email,
        nickname: nickname,
        country: country,
      })
      .then((res) => console.log(res));
  }

  const value = {
    createDBUser,
  };

  return (
    <BackendContext.Provider value={value}>{children}</BackendContext.Provider>
  );
}
