import React from "react";
import PropTypes from "prop-types";

function Button({ onClick, text, color, id }) {
	let data = text;
	return (
		<button
			id={id}
			onClick={onClick}
			style={{ background: color, display: "block" }}
		>
			{text}
		</button>
	);
}

Button.propTypes = {
	text: PropTypes.string,
	color: PropTypes.string,
};

export default Button;
