import React from "react";
import "./UserInfo.css";
import profilePicture from "./assets/Harry Head.png";
import Button from "./../Generic-Components/Button";

function UserInfo({ name, uid, rank }) {
	return (
		<div id="Heading">
			<img id="profile-picture" src={profilePicture} height="100%" />
			<span id="welcome">Welcome,</span>
			<span id="nickname">{name}</span>
			<span id="uid">UID: {uid}</span>
			<span id="rank">Rank: {rank}</span>
		</div>
	);
}

export default UserInfo;
