import "./App.css";
import React from "react";
import { useState, useEffect } from "react";
import LoginButton from "./components/LoginButton";
import LogoutButton from "./components/LogoutButton";
import Profile from "./components/Profile/Profile";
import Friendlist from "./components/Friendlist/Friendlist";
import Leaderboard from "./components/Leaderboard/Leaderboard";
import Home from "./components/Home/Home";
import AboutUs from "./components/AboutUs/AboutUs";
import { useAuth0, User } from "@auth0/auth0-react";
import profile from "./components/Generic-Components/assets/user.png";
import friends from "./components/Generic-Components/assets/friends.png";
import home from "./components/Generic-Components/assets/home.png";
import leader from "./components/Generic-Components/assets/crown.png";
import settings from "./components/Generic-Components/assets/settings.png";
import BackButton from "./components/Generic-Components/BackButton";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import SignUp from "./components/authentication/SignUp";
import { Container, Alert } from "react-bootstrap";
import { AuthProvider } from "./contexts/AuthContext";
import { BackProvider } from "./contexts/BackendContext";
import Login from "./components/authentication/Login";
import { useAuth } from "./contexts/AuthContext";
import { useServer } from "./contexts/BackendContext";
import PrivateRoute from "./components/Generic-Components/PrivateRoute";
import ForgotPassword from "./components/authentication/ForgotPassword";

function App() {
  const { user, isAuthenticated } = useAuth0();

  const [error, setError] = useState("");
  //const { currentUser } = useAuth();

  var displayName = isAuthenticated ? user.name : "user";

  return (
    <Container
      className="d-flex align-items-center justify-content-center"
      style={{ minHeight: "100vh" }}
    >
      <div className="w-100" style={{ maxWidth: "400px" }}>
        <Router>
          <AuthProvider>
            <Switch>
              <Route path="/signup">
                <BackProvider>
                  <SignUp />
                </BackProvider>
              </Route>
              <Route path="/login">
                <Login />
              </Route>
              <Route path="/forgot-password">
                <ForgotPassword />
              </Route>
              <PrivateRoute exact path="/" component={Home}></PrivateRoute>
              <PrivateRoute path="/profile" component={Profile}></PrivateRoute>
              <PrivateRoute
                path="/friend"
                component={Friendlist}
              ></PrivateRoute>
              <PrivateRoute
                path="/leaderboard"
                component={Leaderboard}
              ></PrivateRoute>
              <PrivateRoute path="/aboutus" component={AboutUs}></PrivateRoute>
            </Switch>
          </AuthProvider>
          <footer>
            <Link to="/profile">
              <img id="profile-icon" src={profile} />
            </Link>
            <Link to="/friend">
              <img id="friends-icon" src={friends} />
            </Link>
            <Link to="/">
              <img id="home-icon" src={home} />
            </Link>
            <Link to="/leaderboard">
              <img id="leaderboard-icon" src={leader} />
            </Link>
            <Link to="/aboutus">
              <img id="settings-icon" src={settings} />
            </Link>
          </footer>
        </Router>
      </div>
    </Container>
  );
}

export default App;
