import React, { useState } from "react";
import UserInfo from "../Profile/UserInfo";
import "./Home.css";
import ChoosePlay from "../Home/ChoosePlay";
import { Card, Button, Alert } from 'react-bootstrap';
import { useAuth } from "../../contexts/AuthContext";
import { Link, useHistory } from "react-router-dom";

// import BackButton from "./components/Generic-Components/BackButton";
//import Play from url("https://dummyimage.com/600x400/b039b0/0011ff&text=PLAY!");

function Home() {
  const [playClicked, setPlayClicked] = useState(false);
  const [error, setError] = useState('');
  const { currentUser, logout } = useAuth();
  const history = useHistory();

  const imageURL = "https://dummyimage.com/600x400/b039b0/0011ff&text=PLAY!";

  async function handleLogout() {
    setError('');

    try {
      await logout();
      history.push("/login")

    } catch {
      setError('Failed to Log Out')
    }
  }

  return (
    <>
    {error && <Alert variant="danger">{error}</Alert>}
    <Button onClick={handleLogout}>Log Out</Button>
      <div id="home">
        <UserInfo name={currentUser.email} />
        {playClicked ? (
          <div>
            <ChoosePlay />
          </div>
        ) : (
          <div id="play-button" onClick={() => setPlayClicked(!playClicked)}>
            <img src={imageURL} width="100%" />
          </div>
        )}
      </div>
    </>
  );
}

export default Home;
