import React from "react";
import "./ProfileForm.css";

function ProfileForm() {
	return (
		<div>
			<br />
			<form id="profile-edit">
				<label htmlFor="name-form">Name</label>
				<input type="text" id="name-form" />

				<label htmlFor="nickname-form">Display Name</label>
				<input type="text" id="nickname-form" />

				<label htmlFor="location-form">City, Country</label>
				<input type="text" id="location-form" />

				<label htmlFor="profile-picture-form">Display Picture</label>
				<input type="file" id="profile-picture-form" />
			</form>
			<br />
		</div>
	);
}

export default ProfileForm;
